//int
function int_array(arr) {
  if (!isArray(arr)) {
    return [];
  }
  if (arr.length == 1 && arr[0] == '') {
    return [];
  }
  for (var i = 0, l = arr.length; i < l; i++) {
    arr[i] = int(arr[i]);
  }

  return arr;
}

