
import Vue from 'vue'
import moment from 'moment'
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


Vue.use(DatetimePlugin)

const emptyObject = {
bid: 0,
mc_id: 0,
name: '',
tel: '',
email: '',
sex: 1,
birth_time: moment(new Date()).subtract(6,'years').format('YYYY-01-01'),
grade: '',
remark: '',
is_valid: 0,
qr_eid: 0,
assigned_eid: 0,
recommend_reward_note: '',
code: ''
}

export default{
mixins: [ common, wxjssdk ],
components: {
Msg,
Divider,
XButton
},
data () {
return {
TEXT_VCODE: '',
VCODE_LIMIT_SECONDS: 0,
vcode_sending: false,
icon: 'success',
info: util.copy(emptyObject),
showResult: false,
countDown: 5,
interval: null,
config: {},
msg: {
icon:'success',
title: '',
description: '!'
}
}
},
mounted () {
this.init_config()
},
computed: {
disabled () {
return !/^1\d{10}$/.test(this.info.tel) || this.VCODE_LIMIT_SECONDS > 0
},
vcode_text () {
if(this.VCODE_LIMIT_SECONDS > 0){
return this.VCODE_LIMIT_SECONDS + ''
}
return this.TEXT_VCODE
},
buttons(){
return  [{
type: 'primary',
text: `${this.countDown}s `,
onClick:  this.back.bind(this)
}]
},
enableMustFieldsList () {
let result = []
if(this.config.must_fields && this.config.must_fields.length > 0){
result = this.config.must_fields.filter(f=>f.enable)
}
return result
},
enableFieldsList () {
let result = []
if(this.config.fields && this.config.fields.length > 0){
result = this.config.fields.filter(f=>f.enable)
}
return result
},
logo () {
let ui = window.CLIENT_PARAMS,
result = 'https://sp1.xiao360.com/static/img/logo.png'
if(!_.isEmpty(ui)&&!_.isEmpty(ui.mobile)&&ui.mobile.logo!=''){
result = ui.mobile.logo
}
if(this.config.logo){
result = this.config.logo
}
return result
}
},
methods: {
send_vcode(){
if(this.disabled) {
return
}

let params = {type:'qrsign'}
params.mobile = this.info.tel
this.vcode_sending = true
this.$http.post('open/vcode',params)
.then(response=>{
this.VCODE_LIMIT_SECONDS = 120
this.loop_check_vcode()
},response=>{
this.toast(response.body.message||'','warn')
this.vcode_sending = false
})
},
loop_check_vcode(){
this.VCODE_LIMIT_SECONDS--
if(this.VCODE_LIMIT_SECONDS <= 0){
this.VCODE_LIMIT_SECONDS = 0
this.vcode_sending = false
return
}
setTimeout(()=>{
this.loop_check_vcode()
},1000)
},
countStart () {
if(this.countDown == 0){
if(this.config.msg.redirect_url){
window.open(this.config.msg.redirect_url)
}else{
this.back()
}
return
}

if(this.interval){ window.clearTimeout(this.interval) }
this.interval = setTimeout(()=>{
this.countDown --
this.countStart()
},1000)
},
setMsg (icon,title,description) {
this.msg.title = title
this.msg.description = description
this.msg.icon = icon
},
ok () {
if(/^\s*$/.test(this.info.name)){
this.toast('','warn')
return
}
if(/^\s*$/.test(this.info.tel)){
this.toast('','warn')
return
}
if(!/^1\d{10}$/.test(this.info.tel)){
this.toast('','warn')
return
}

this.info.bid = this.$route.query.bid||0
this.info.mc_id = this.$route.query.mc_id||0
this.info.qr_eid = this.$route.query.eid||0
this.info.assigned_eid = this.$route.query.eid||0

this.showLoading()

this.$rest('open/qr_clue')
.post(this.info)
.success(data=>{
this.hideLoading()
this.setMsg('success',this.config.msg.title,this.config.msg.description)
this.showResult = true
this.countStart()
})
.error(response=>{
this.toast(response.body.message||'','warn')
this.hideLoading()



})


},
showPlugin () {
let $this = this,
default_date = $this.info.birth_time
if(this.config.must_fields && this.config.must_fields[0].default_value){
default_date = this.config.must_fields[0].default_value
}

this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM-DD',
startDate: moment(new Date()).subtract(30,'years').format('YYYY-MM-DD'),
value: default_date,
endDate: moment(new Date()).subtract(1,'years').format('YYYY-MM-DD'),
onConfirm (val) {
$this.info.birth_time = val
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
back () {
this.info = util.copy(emptyObject)
this.setMsg('success',this.config.msg.title,this.config.msg.description)
this.showResult = false
},
setDefaultInfo () {
this.config.must_fields.forEach(m=>{
if(m.default_value){
this.info[m.field] = m.default_value
}
})
this.config.fields.forEach(f=>{
if(f.default_value){
this.info[f.field] = f.default_value
}
})
},
init_config () {
this.init_qr_config()
.then(resolve=>{
this.setDefaultInfo()
},reject=>{
this.$rest('/sapi/open/global/?name=configs')
.get()
.success(data=>{
this.config = data.qrsign
this.setDefaultInfo()
}).error(response=>{
this.toast(response.body.message||'','warn')
})
})
},
init_qr_config () {
return new Promise((resolve,reject)=>{
this.$rest(`open/qr_config?mc_id=${this.$route.query.mc_id}`)
.get()
.success(data=>{
if(!_.isEmpty(data)){
this.config = data
resolve()
}else{
reject()
}
}).error(response=>{
reject()
this.toast(response.body.message||'~','warn')
})
})
}
}
}



