
export default {
	data () {
		return {
			images: {
				serverId: [],
				localId: [],
				urls: []
			},
			uploadStatus: false
		}
	},
    methods: {
    	showLoading () {
	        this.$vux.loading.show({
	          text: 'Loading'
	        })
	        
	        if(this.loadingInterval){
	        	clearTimeout(this.loadingInterval)
	        }
	    },
	    hideLoading () {
	    	setTimeout(() => {
	          this.$vux.loading.hide()
	        }, 800)
	    },
    	checkJsApi () {
    		return new Promise((resolve,reject)=>{
    			this.$store.dispatch('getJssdkConfig',this.$wechat)
       			.then(jssdk=>{
	    			this.$wechat.checkJsApi({
				      jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline','onMenuShareQQ','onMenuShareWeibo'],
				      success: function (res) {
				        alert(JSON.stringify(res));
				        resolve()
				      }
				    });
			    })
    		})
    	},
    	setWxShare(title,img,desc,link){
    		link = link || window.location.href
    		
    		let sub_host = this.$store.state.user.info?this.$store.state.user.info.x_sub_host:''
    		
    		if(link.indexOf(sub_host)==-1){
    			link = link.replace('//','//'+sub_host+'.')
    		}
            return new Promise((resolve,reject)=>{
                this.$store.dispatch('getJssdkConfig',this.$wechat)
                .then(jssdk=>{
                   let funcs =  ['onMenuShareAppMessage','onMenuShareTimeline','onMenuShareQQ','onMenuShareWeibo']
                   let share_data = {}
                   share_data.title = title
                   share_data.link  = link || window.location.href
                   share_data.imgUrl = img
                   share_data.desc   = desc||''
                   share_data.success = function(){
                    resolve()
                   }
                   share_data.cancel = function(){
                    reject()
                   }
                   
                   //configreadyconfigconfigready
               	   //https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
                   this.$wechat.ready(()=>{
                   		funcs.forEach((func,i)=>{
	                   		this.$wechat[func](share_data)                   		
	                   	})
                   })
                })
            })
       },       
       chooseWXPay (data) {
       		return new Promise((resolve,reject)=>{
       			this.$store.dispatch('getJssdkConfig',this.$wechat)
       			.then(jssdk=>{
       				this.$wechat.chooseWXPay({
       					timestamp: data.timeStamp,
					    nonceStr: data.nonceStr,
					    package: data.package,
					    signType: data.signType,
					    paySign: data.paySign,
					    success: function (res) {
					    	resolve()
					        // 
					        console.log('!')
					    },
					    error: function (res) {
					    	reject()
					    	console.log(res)
					    	console.log('')
					    }
       				})       				
       			})
       		})
        },
        previewImage (current,list) {
       		this.$wechat.previewImage({
       			current: current, // http
   				urls: list // http
       		})
        },
        chooseImage (count,upload) {
        	count = count || 9
        	return new Promise((resolve,reject)=>{
        		this.$store.dispatch('getJssdkConfig',this.$wechat)
       			.then(jssdk=>{
	        		this.$wechat.chooseImage({
		        		count: count, // 9
						sizeType: ['original', 'compressed'], // 
						sourceType: ['album', 'camera'], // 
				        success: (res) => {
					        this.images.localId = res.localIds;	
					        if(upload){
						        this.uploadImage().then(()=>{
						        	resolve(this.images.urls)
						        })
					        }else{
					        	resolve(res.localIds)
					        }
				      	}
				    });
			    })
        	})
        },
        uploadImage () {
        	let images = this.images
        	if (images.localId.length == 0) {
		      alert('');
		      return;
		    }
		    let i = 0, length = images.localId.length;
		    let $this = this
		    images.serverId = [];
		    images.urls = [];		    
		    
		    return new Promise((resolve,reject)=>{
		    	function upload() {
			      	$this.$wechat.uploadImage({
				        localId: images.localId[i],
				        success: (res) => {				          
				          	images.serverId.push(res.serverId);
				          
				          	$this.downloadImage(res.serverId).then(data=>{
					          	i++;
					          	$this.images.urls.push(data)
					          	if (i < length) {
						            upload();
						        }else{
						          	resolve()
						        }
				          	})				          
				        },
				        fail: (res) => {
				          	alert(JSON.stringify(res));
				          	reject();
				        }
			      	});
			    }
			    upload();	
	    	})
		    	    		    
        },
        uploadSingleImage (localId) {
        	return new Promise((resolve,reject)=>{
        		this.$wechat.uploadImage({
			        localId: localId,
			        success: (res) => {
			          resolve(res.serverId)
			        },
			        fail: (res) => {
			          alert(JSON.stringify(res));
			        }
	        	})
        	})        	
        },
        //media_idurl
        downloadImage (serverId) {        	
        	return new Promise((resolve,reject)=>{
        		this.$rest('/sapi/users/wx_upload_image').post({serverId:serverId})
	        	.success(response=>{
	        		resolve(response.data[0])
	        	}).error(response=>{
	        		reject()
	        		alert(response.body.message||'')
	        	})    
        	})
    		    	 	
        },
        getlocalImageData (localId) {
        	return new Promise((resolve,reject)=>{
        		this.$wechat.getLocalImgData({
					localId: localId, // localID
					success: function (res) {
						resolve(res.localData) 	//localDatabase64img
					},
					error: function (res) {
						reject(res)
					}
				});
        	})        	
        },
        startRecord () {
        	return new Promise((resolve,reject)=>{
        		this.$wechat.startRecord({
	        		cancel: function () {
	        			reject()
	        			alert('')
	        		},
	        		success: function () {
	        			resolve()
	        		}
	        	})
        	})        	
        },
        onVoiceRecordEnd () {
          return new Promise((resolve,reject)=>{
            this.$wechat.onVoiceRecordEnd({
              //  complete 
              complete: (res) => {  
                this.uploadVoice(res.localId)
                  .then(data=>{
                    resolve(data)
                  },response=>{
                    alert(JSON.stringify(response))
                    reject(response)
                  })
              }
            });
          })
        },
        stopRecord () {
          return new Promise((resolve,reject)=>{
            this.$wechat.stopRecord({
              success: (res) => {
//              resolve(res.localId)                
//            resolve(res.localId)     
              this.uploadVoice(res.localId)
              .then(data=>{
                resolve(data)
              },response=>{
                reject(response)
              })
              },
              fail: function (res) {
                reject(res)
                alert(JSON.stringify(res))
              }
            })
          })
        },
        uploadVoice (localId) {
          return new Promise((resolve,reject)=>{
            this.$wechat.uploadVoice({
              localId: localId,
              success: (res) => {           
  //              resolve(res.serverId)
                // alert(res.serverId)
                this.downloadWXFile(res.serverId,'voice')
                .then(data=>{
                  resolve(data)
                },res=>{
                  reject(res)
                })
              },
              fail: function (res) {
                reject(res)
              }
            })
          })          
        },
        downloadVoice (serverId) {
          return new Promise((resolve,reject)=>{
            this.$wechat.downloadVoice({
              serverId: serverId,
                success: function (res) {             
                  resolve(res.localId)
                },
                fail: function (res) {
                  reject(res)
                }
            });
          })
        },
        downloadWXFile (media_id,type) {
          let params = {
            msg_type: type,
            media_id: media_id
          }
          // alert(JSON.stringify(params))          
          this.showLoading()
          return new Promise((resolve,reject)=>{
            this.$rest('/sapi/common/wx_download_file')
            .post(params)
            .success(response=>{
             // alert(JSON.stringify(response.data[0]))              
              this.hideLoading()
              resolve(response.data[0])
            }).error(response=>{
            	this.hideLoading()
              alert(response.body.message||'')
              reject(response)
            })
          })          
        },
        playVoice (localId) {
        	if(!localId){
        		alert('Id')
        		return 
        	}
        	this.$wechat.playVoice({
		      localId: localId
		    });
        },
        stopVoice (localId){
        	if(!localId){
        		alert('Id')
        		return 
        	}
        	this.$wechat.stopVoice({
		      localId: localId
		    });
        },
        onVoicePlayEnd (localId) {
        	return new Promise((resolve,reject)=>{
        		this.$wechat.onVoicePlayEnd({
        			success: function (res) {
						resolve()
					}
        		})
        	})
        },
        getWXFile (list) {           		
       		let result = [],
       			method = '',
       			i = 0,
       			$this = this
       			
       		list = list||[]
       		
       		return new Promise((resolve,reject)=>{
       			function upload () {      
           			if(list[i].file_type=='image'){
       					method = 'uploadSingleImage'  						
       				}else if(list[i].file_type=='voice'){
       					method = 'uploadVoice'           					
       				}
           			
           			$this[method](list[i].file_url)
       				.then(media_id=>{
       					$this.downloadWXFile(media_id,list[i].file_type)
   						.then(response=>{
   							i++
   							result.push(response.data[0])
   							
   							if(i < list.length){
		           				upload()
			           		}else{
			           			resolve(result)
	   						}
       					},response=>{
       						reject(response)
       					})
           			})
       			}
           		if(list.length){           			
           			upload()
           		}
       		})
        }
    }
}


